.mainPage__container {
  margin: auto;
  padding: 35px 123px;
  align-items: center;
  justify-content: center;
  max-width: 2000px;

  .paintings__wrapper {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 275px;
  }

  @media (max-width: 1366px) {
    padding: 35px 42px;
    .paintings__wrapper {
      display: grid;
      gap: 20px;
      justify-content: center;
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: 230px;
    }
  }

  @media (max-width: 1024px) {
    padding: 35px 34px;
    .paintings__wrapper {
      display: grid;
      gap: 20px;
      justify-content: center;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 250px);
    }
  }
  @media (max-width: 768px) {
    max-width: 550px;
    padding: 25px 20px;
    .paintings__wrapper {
      display: grid;
      gap: 20px;
      grid-auto-rows: 205px;
      grid-template-columns: 1fr;
    }
  }
}